<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code> <span class="token keyword">public</span> <span class="token keyword">function</span> <span class="token function-definition function">getdoc</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
<span class="token punctuation">{</span>
    <span class="token variable">$phpWord</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name class-name-fully-qualified"><span class="token punctuation">\</span>PhpOffice<span class="token punctuation">\</span>PhpWord<span class="token punctuation">\</span>PhpWord</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token variable">$section</span> <span class="token operator">=</span> <span class="token variable">$phpWord</span><span class="token operator">-></span><span class="token function">addSection</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token variable">$text</span> <span class="token operator">=</span> <span class="token variable">$section</span><span class="token operator">-></span><span class="token function">addText</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"hello"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token variable">$objWriter</span> <span class="token operator">=</span> <span class="token class-name class-name-fully-qualified static-context"><span class="token punctuation">\</span>PhpOffice<span class="token punctuation">\</span>PhpWord<span class="token punctuation">\</span>IOFactory</span><span class="token operator">::</span><span class="token function">createWriter</span><span class="token punctuation">(</span><span class="token variable">$phpWord</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'Word2007'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token variable">$objWriter</span><span class="token operator">-></span><span class="token function">save</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'Appdividend.docx'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
